export function versionGroupNameToGame(str) {
	str = str.toLowerCase();
	if (str === "ultra-sun-ultra-moon") {
		return ["ultra sun", "ulra moon"]; 
	}
	if (str === "black-2-white-2") {
		return ["black 2", "white 2"]
	}
	if (str === "let's-go,-pikachu!-let's-go,-eevee!") {
		return 	["let's go, pikachu!", "let's go, eevee!"];
	}
	return str.split("-");
}

export function byGameRelease(a,b) {
	a = games[a.toLowerCase()]; 
	b = games[b.toLowerCase()];
	return a - b; 
}

/**
 * Rounds number to the decimals place. 
 */
export function round(number, decimals) {
	const t = Math.pow(10,decimals);
	return Math.round(number * t ) / t;
}

export function mod(number, mod) {
	return ((number % mod) + mod) % mod; 
}

/** 
 * A generic loader. 
 */
export function LoaderElement() {
	return (
		<div className="loader-container">
			<Loader active inline='centered'>Loading</Loader>
		</div>
	);
}

/**
 * Hook that alerts clicks outside of the passed ref
 */
function useOutsideAlerter(ref, callback) {
  /**
   * Alert if clicked on outside of element
   */
  function handleClickOutside(event) {
    if (ref.current && !ref.current.contains(event.target)) {
      callback(); 
    }
  }

  useEffect(() => {
    // Bind the event listener
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      // Unbind the event listener on clean up
      document.removeEventListener("mousedown", handleClickOutside);
    };
  });
}

/**
 * Component that alerts if you click outside of it.
 */
export function OutsideAlerter(props) {
  const wrapperRef = useRef(null);
  useOutsideAlerter(wrapperRef, props.callback);
  return <div ref={wrapperRef}>{props.children}</div>;
}

export function get(url,callback) {
	const xhr = new XMLHttpRequest(); 
	xhr.addEventListener('load', () => {
		callback(JSON.parse(xhr.responseText));
	})
	xhr.open('GET', url);
	xhr.send();
}

