import React from 'react';
import { Icon } from 'semantic-ui-react';
import { OutsideAlerter, leadingZero } from '../../util.module.js';
import './dropdown.scss';
import '../../util.scss';

/**
 * Given a list of options and a callback function to call on 
 * selection event, creates a Dropdown component. 
 * Events: 
 * 		- Enter: Selects the current element and calls callback 
 *    - ArrowUp/ArrowDown: Changes the currently selected element.
 *		- Backspaces: Updates placeholder, scrolltop, and filtered list.
 *		- Click outside: Closes option list. 
 */
export default class Dropdown extends React.Component {
	constructor(props) {
		super(props); 
		this.state = {
			input: props.input, 
			showOptions: false, 
			currentOptionIndex: 0,
			placeHolder: props.placeHolder,
		}
	}

	static getDerivedStateFromProps(nextProps, prevState) {
		if (nextProps.input !== prevState.input) {
			return {
				input: nextProps.input,
			};
		}
		return prevState;
	}

	/**
	 * Case-insensitive prefix matches. 
	 */
	filter = (prefix, arr) => {
		arr = arr.map((item,i) => {return {name: item, id: leadingZero(i+1, 3)}});
		if (!isNaN(prefix)) {
			return arr.filter(item => item.id.includes(prefix.toString(10)));
		}
		return arr.filter(
			item => item.name.toUpperCase().slice(0,prefix.length) === prefix.toUpperCase()
		); 
	}

  /**
   * Fires when user types into the input. Updates input and related 
   * state attributes. 
   */
	handleChange = e => {
		this.setState({
			showOptions: true,
			currentOptionIndex: 0,
		});
		this.props.onInputValue(e.target.value);
	};

	/** 
	 * Fires on clicking on an option. Calls the callback function and 
	 * updates the state.
	 */
	handleClick = name => {
		this.props.onSelection(name); 
		this.setState({
			showOptions: false,
			currentOptionIndex: 0,
		});
		this.props.onInputValue(name);
	}

	/**
	 * Fires on Enter key. Selects the current index and calls callback
	 * function.
	 */
	handleKeyPress = e => {
		e.persist(); 
		if (e.key === "Enter") {
			const newInput = this.filter(
				this.state.input.trim(),this.props.options
			)[this.state.currentOptionIndex].name; 
			this.setState({
				showOptions: false,
				currentOptionIndex: 0,
			});
			this.props.onInputValue(newInput);
			this.props.onSelection(newInput); 
		} 
	}

	/** 
	 * Updates current index based on arrow key. 
	 */
	handleKeyDown = e => {
		const filteredOptions = this.filter(
			this.state.input, this.props.options
		);
		const length = filteredOptions.length;
		let i = this.state.currentOptionIndex; 
		if (e.key === "ArrowDown") {
			if (i === length - 1) {
				return;
			}
			i++;
		} else if (e.key === "ArrowUp") {
			if (i === 0) {
				return;
			}
			i--;
		}
		if (i < filteredOptions.length) {
			this.setState({
				currentOptionIndex:i, 
				showOptions: true,
				placeHolder: filteredOptions[i].name,
			});
		}
	}

	/** 
	 * Forcefully scroll the container if option is out of view.
 	 */	
	handleKeyUp = e => {	
		const optionsElement = document.getElementById("options");
		if (["ArrowDown", "ArrowUp"].includes(e.key) && optionsElement) {
			const curr = this.state.currentOptionIndex * 39; 
			if (curr < optionsElement.scrollTop || 
					curr > (optionsElement.scrollTop + 39*3)) {
				optionsElement.scrollTop = curr; 
			}
		}
		const filteredOptions = this.filter(
			this.state.input, this.props.options
		);
		if (e.key === "Backspace" && optionsElement) {
			optionsElement.scrollTop = 0;
			this.setState({
				currentOptionIndex: 0,
				placeHolder: filteredOptions[0].name,
			}); 
		}
	}

	/**
	 * Returns JSX of a list of options if filtered option's length is 
	 * greater than 0, returns null otherwise. 
	 */	
	OptionsElement = () => {
		const filteredOptions = this.filter(this.state.input,this.props.options); 
		if (filteredOptions.length === 0) {
			return null; 
		}
		return (
			<div className="options-container absolute">
				<div className="options" id="options">
					{
						filteredOptions.map((option,i) => 
							{
								let id = '';
								if (i === this.state.currentOptionIndex) {
									id = "selected";
								}
								return (
									<div
										className="option"
										id={id} 
										onClick={()=> this.handleClick(option.name)}
										key={option.name}>
										<div className="flex">
											<div className="dropdown-id">{option.id}</div>
											<div className="dropdown-name">{option.name}</div>
										</div>
									</div>
								);
							}
						)
					}
				</div>
			</div>
		);
	}

	render() {
		let options; 
		if (this.state.showOptions) {
			options = this.OptionsElement();
		}
		let dropdownStyle = {
			"borderRadius": (this.state.showOptions && options ? "8px 8px 0 0" : "8px"),
			"borderColor": (this.state.showOptions ? "#9ec0ff" : "#80808061"),
		}

		return (
			<OutsideAlerter callback={() => this.setState({showOptions: false})}>
				<div className="dropdown relative" style={dropdownStyle}>
					<Icon className="search" name="search"/>
					<input 
					  placeholder={this.state.placeHolder} 
					  spellCheck="false"
					  value={this.state.input || ''}
			  		onClick={()=>this.setState({showOptions: true})}
				  	onChange={this.handleChange}
					  onKeyPress={this.handleKeyPress}
					  onKeyDown={this.handleKeyDown}
					  onKeyUp={this.handleKeyUp}/>
					{options}
				</div>
			</OutsideAlerter>
		);
	}
}