import React from 'react';
import { P, alphabetical, round, LoaderElement, get, leadingZero, dmgMap, games, versionGroupNameToGame } from '../../util.module.js';
import { Modal, Tab } from 'semantic-ui-react';

import '../../util.scss';
import './pokemon-layout.scss';

export default class PokemonLayout extends React.Component {

	// Mounting Lifecycles ---------------------------------------------

	constructor(props) {
		super(props);
		this.state = {
			pokemon: props.pokemon,
			error: false,
			initializing: false,
			locationEncounters: [],
			species: null,
			abilities: [],
			heldItems: [],
			evolutionMap: [],
			types: [],
			moves: [],
		};
		this.updateAll();
	}

	static getDerivedStateFromProps(nextProps, prevState) {
		if (nextProps.pokemon.id !== prevState.pokemon.id) {
			return {
	 		  pokemon: nextProps.pokemon,
			  error: false,
			  initializing: false,
			  locationEncounters: [],
			  species: null,
			  abilities: [],
			  heldItems: [],
			  moves: [],
			  evolutionMap: [],
			  types: [],
			};
		}
		return null;
	}
	// Methods ---------------------------------------------------------

	updateLocationEncounters = () => {
		get(this.props.pokemon.location_area_encounters, response => {
			this.setState({locationEncounters: response});
		});
	}

	updateSpecies = () => {
		get(this.props.pokemon.species.url, response => {
			this.setState({species: response});
			get(response.evolution_chain.url, response => {
				let queue = [response.chain];
				let element = [];
				while (queue.length) {
					const curr = queue.shift();
					element.push(curr.species.name);
					curr.evolves_to.forEach(chain => queue.push(chain));
				}
				for (let i = 0; i < element.length; i++) {
					P.getPokemonByName(element[i], (response,error) => {
						if (!error) {
							const evolutionMap = this.state.evolutionMap.slice();
							evolutionMap.push([i,response]);
							this.setState({evolutionMap});
						} else {
							console.error(error);
						}
					});
				}
			});
		});
	}

	updateAbilities = () => {
		this.props.pokemon.abilities.forEach(ability => {
			get(ability.ability.url, response => {
				const abilities = this.state.abilities.slice(); 
				abilities.push(response);
				this.setState({abilities});
			})
		});
	}

	updateHeldItems = () => {
		this.props.pokemon.held_items.forEach(heldItem => {
			get(heldItem.item.url, response => {
				const heldItems = this.state.heldItems.slice(); 
				heldItems.push(response);
				this.setState({heldItems});
			});
		});
	}

	updateMoves = () => {
		console.log(this.props.pokemon.moves);
		this.props.pokemon.moves.forEach(move => {
			get(move.move.url, response => {
				const moves = this.state.moves.slice(); 
				moves.push(response);
				this.setState({moves});
			});
		});
	}

	updateTypes = () => {
		this.props.pokemon.types.forEach(type => {
			get(type.type.url, response => {
				const types = this.state.types.slice(); 
				types.push(response);
				this.setState({types});
			});
		})
	}

	updateAll = () => {
		this.updateLocationEncounters();
		this.updateSpecies();
		this.updateAbilities();
		this.updateHeldItems();
		this.updateMoves();
		this.updateTypes(); 
	}

	// Functional Components -------------------------------------------

	AbilitiesElement = () => {
		if (!this.state.abilities || !this.state.abilities.length) {
			return null; 
		}
		return (
			<div className="pokemon-container flex-column">
				<div className="bold">Abilities</div>
				<div className="abilities flex">
					{
						this.state.abilities.map(ability  => {
							let effect;
							const found = this.props.pokemon.abilities.find(item => item.ability.name === ability.name);
							let hidden = ''; 
							if (found && found.is_hidden) {
								hidden = ' (Hidden)'
							} 
							if (ability.effect_entries.length) {
								effect = (
									<div className="text-align-center">
										{ability.effect_entries[0].effect}
									 </div>
								);
							}
							return (
								<Modal trigger={<button>{ability.name + hidden}</button>} key={ability.name}>
							    <Modal.Content>
							      <Modal.Description>
							      {effect}
							      </Modal.Description>
							    </Modal.Content>
							  </Modal>
							);
						})
					}
				</div>
			</div>
		);
	}

	BaseExpElement = () => {
		return (
			<div className="pokemon-container">
				<div className="label">Base EXP</div>
				<div>
					{this.state.pokemon.base_experience + " EXP"}
				</div>
			</div>
		);
	}

	HeightElement = () => {
		const height = (this.state.pokemon.height * 10)/30.48;
		return (
			<div className="pokemon-container height">
				<div className="label">Height</div>
				<div style={{'textTransform':'lowercase'}}>
					{round(height,2) + ' ft'}
				</div>
			</div>
		);
	}

	BaseHappinessElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Base Happiness</div>
				<div>
					{this.state.species.base_happiness} / 255
				</div>
			</div>
		);
	}

	CaptureRateElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Capture Rate</div>
				<div>
					{this.state.species.capture_rate} / 255
				</div>
			</div>
		);
	}

	GenerationElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Generation</div>
				<div style={{"textTransform":"uppercase"}}>
					{this.state.species.generation.name.split("-")[1]}
				</div>
			</div>
		);
	}

	GrowthElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Growth Rate</div>
				<div>
					{this.state.species.growth_rate.name}
				</div>
			</div>
		);
	}

	HabitatElement = () => {
		if (!this.state.species || !this.state.species.habitat || 
			  !this.state.species.habitat.name) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Habitat</div>
				<div>
					{this.state.species.habitat.name}
				</div>
			</div>
		);
	}

	HatchElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Hatch Counter</div>
				<div>
					{Number(255*(this.state.species.hatch_counter + 1)).toLocaleString()} Steps
				</div>
			</div>
		);
	}

	NameElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="name-container">
				<div className="label">{this.state.pokemon.name}</div>
				<div className="id">{leadingZero(this.state.species.id, 3)}</div>
			</div>
		);		
	}

	MovesElement = (move) => {
		let effect; 
		if (move.effect_entries.length) {
			effect = move.effect_entries[0].effect;
		}
		let accuracy; 
		if (move.accuracy) {
			accuracy = 	(<div className="flex">
					      	<div>Accuracy</div>
					      	<div>{move.accuracy}</div>
					    </div>);
		}
		let power; 
		if (move.power) {
			power = (<div className="flex">
			      		<div>Power</div>
			      		<div>{move.power}</div>
			      	</div>);
		}
		return (
			<Modal trigger={<button className="move-button">{move.name}</button>} key={move.name}>
			    <Modal.Content>
			      <Modal.Description>
				      <div className="flex">
				        <div className="flex-column description-box">
				        	{accuracy}
					      	<div className="flex">
					      		<div>Type</div>
					      		<div>{move.type.name}</div>
					      	</div>
					      	{power}
					      	<div className="flex">
					      		<div>pp</div>
					      		<div>{move.pp}</div>
					      	</div>
				    	</div>
				      	<div className="description-effect">{effect}</div>
				      </div>
			      </Modal.Description>
			    </Modal.Content>
			</Modal>
		);
	}

	SpritesElement = () => {
		const sprites = Object.entries(this.state.pokemon.sprites)
													.filter(([name,url]) => url != null);
		sprites.sort((a,b) => alphabetical(a[0],b[0]));
		if (sprites.length === 0) {
			return null;
		}
		return (
			<div className="pokemon-container flex sprites-element">
				{
					sprites.map(([name,url]) => 
						{
							return (
								<div className="flex-column" key={name}>
									<img src={url} alt={name}/>
								</div>
							);
						}
					)
				}
			</div>
		);
	}

	StatsElement = () => {
		const stats = this.state.pokemon.stats;
		stats.sort((a,b) => alphabetical(a.stat.name, b.stat.name));
		return (
			<div className="pokemon-container flex-column">
				<table className="stat">
					<thead>
						<tr>
							<th>Stat</th>
							<th>Base</th>
							<th>EV</th>
						</tr>
					</thead>
					<tbody>
					{
						stats.map(stat => 
							{ 
								return (
									<tr key={stat.stat.name}>
										<td>{stat.stat.name}</td>
										<td>{stat.base_stat}</td>
										<td>{stat.effort}</td>
									</tr>
								);
							}
						)
					}
					</tbody>
			  </table>
			</div>
		);
	}

	TypesElement = () => {
		const names = this.state.pokemon.types.map(type => type.type.name);
		names.sort();
		return (
			<div className="type-container">
				<div className="flex types">
					{
						names.map(name => 
							<button key={name} className={"cancel " + name}>{name}</button>
						)
					}
				</div>
			</div>
		);
	}

	WeightElement = () => {
		return (
			<div className="pokemon-container weight">
				<div className="label">Weight</div>
				<div style={{"textTransform": "lowercase"}}>
					{round(this.state.pokemon.weight / 4.536, 2) + " lbs"}
				</div>
			</div>
		);
	}

	HeldItemsElement = () => {
		if (!this.state.heldItems.length) {
			return null;
		}
		const items = this.state.heldItems;
		items.sort((a,b) => alphabetical(a.name, b.name));
		return (
			<div className="pokemon-container flex-column special">
				<div className="bold">Held Items (Wild)</div>
				<div className="flex held-items">
					{
						items.map(item => {
							let effect; 
							if (item.effect_entries.length) {
								effect = item.effect_entries[0].effect;
							}
							let source; 
							if (item.sprites && item.sprites.default) {
								source = item.sprites.default;
							}
							return (
								<Modal trigger={<button>{item.name}</button>} key={item.name}>
							    <Modal.Content>
							      <Modal.Description>
							      	<div className="flex">
												<img src={source} alt={item.name}/>
												<div className="held-effect">{effect}</div>
											</div>
							      </Modal.Description>
							    </Modal.Content>
							  </Modal>
						  );
						})
					}
				</div>
			</div>
		);
	}

	TabsElement = () => {
		if (!this.state.species || !this.state.species.flavor_text_entries ||
			!this.state.species.flavor_text_entries.length || 
			!this.state.locationEncounters.length || this.state.moves.length !== this.state.pokemon.moves.length) {
			return null; 
		}
		const entries = new Map(this.state.species.flavor_text_entries
			.filter(entry => entry.language.name === "en")
			.map(entry => [
				entry.version.name.replace(/-/g, " "), 
				entry.flavor_text.replace("", " ")
			])
		);
		const gameToLocations = new Map(); 
		this.state.locationEncounters.forEach(locationEncounter => {
			const location = locationEncounter.location_area.name;
			locationEncounter.version_details.forEach(version => {
				const name = version.version.name.replace(/-/g, " ");
				if (gameToLocations.has(name)) {
					gameToLocations.get(name).push({version,location});
				} else {
					gameToLocations.set(name, [{version,location}]);
				}
			});
		});
		const gameToMoves = new Map();
		this.props.pokemon.moves.forEach(move => {
			const { name } = move.move;
			move.version_group_details.forEach(detail => {
				const games = versionGroupNameToGame(detail.version_group.name);
				games.forEach(game => {
					if (gameToMoves.has(game)) {
						gameToMoves.get(game).push({detail, name});
					} else {
						gameToMoves.set(game, [{detail, name}]);
					}
				})
			});
		});
		const panes = Object.keys(games).filter(game => entries.has(game) || gameToLocations.has(game)).map(game => {
			return {
				menuItem: game, 
				render: () => {
					let entry; 
					if (entries.has(game)) {
						entry = (
							<div className="flex-column pokedex-entry">
								<div className="title">Pokédex Entry</div>
								<div className="entry">{entries.get(game)}</div>
							</div>
						);
					}
					let locations; 
					if (gameToLocations.has(game)) {
						locations = this.LocationElement(gameToLocations.get(game));
					}
					let moves; 
					if (gameToMoves.has(game)) {
						moves = this.TabMovesElement(gameToMoves.get(game));
					}
					return (
						<Tab.Pane>
							{entry}
							{locations}
							{moves}
						</Tab.Pane>
					);
				}
			};
		});
  		return (
  			<div className="flex-column tabs-element">
  				<div className="pokemon-games">Generations</div>
	  			<Tab 
	  				className="tabs" 
	  				menu={{ fluid: false, vertical: false, tabular: false }} 
	  				panes={panes} />
  			</div>
  		);
	}

	TabMovesElement = (moves) => {
		const methodToMove = new Map(); 
		moves.forEach(move => {
			const { name } = move.detail.move_learn_method;
			if (methodToMove.has(name) && !methodToMove.get(name).map(a => a.name).includes(move.name)) {
				methodToMove.get(name).push(move);
			} else {
				methodToMove.set(name,[move]);
			}
		});
		methodToMove.get("machine").sort((a,b) => alphabetical(a.name, b.name));
		methodToMove.get("level-up").sort((a,b) => a.detail.level_learned_at - b.detail.level_learned_at);
		return ( 
			<div className="flex tab-moves-element">
				<div className="flex-column">
					<div className="title">Level Up Moves</div>
					<div className="flex-column tab-moves">
					{
						methodToMove.get("level-up").map(move => {
							const detail = this.state.moves.find(item => item.name === move.name);
							return this.MovesByLevelUpElement(detail, move.detail.level_learned_at);
						})
					}
					</div>
				</div>
				<div className="flex-column">
					<div className="title">Machine Moves</div>
					<div className="flex tab-moves">
					{
						methodToMove.get("machine").map(move => {
							const detail = this.state.moves.find(item => item.name === move.name);
							return this.MovesElement(detail);
						})
					}
					</div>
				</div>
			</div>
		);
	}

	MovesByLevelUpElement = (detail, level) => {
		return (
			<div className="flex detail" key={detail.name}>
				<div className="detail-level">{level}</div>
				{this.MovesElement(detail)}
			</div>
		);
	}

	LocationElement = (locations) => {
  		return (
  			<div className="flex-column special-locations">
	  			<div className="habitat">Habitat</div>
	  			<div className="flex region-container">
	  				{
	  					locations.map(location => {
	  						const region = location.location;
	  						const version = location.version;
	  						return (
	  							<div className="flex-column location" key={region}>
	  								<div className="region-name label">{region.replace(/-/g, " ")}</div>
										<div className="flex encounters">
	  									{
	  										version.encounter_details.map((encounter, i) => {
	  											let conditionElement; 
	  											if (encounter.condition_values.length) {
	  												conditionElement = (
	  													<div className="flex">
	  														<div className="label">Condition</div>
	  														<div>{encounter.condition_values.map(condition => condition.name).join(", ").replace(/-/g, " ")}</div>
	  													</div> 
	  												);
	  											}
	  											let level = (
														<div className="flex">
															<div className="label">Level</div>
															<div>{encounter.max_level}</div>
														</div>
	  											); 
	  											if (encounter.max_level !== encounter.min_level) {
	  												level = (
	  													<React.Fragment>
																<div className="flex">
		  														<div className="label">Max LVL</div>
		  														<div>{encounter.max_level}</div>
		  													</div>
		  													<div className="flex">
		  														<div className="label">Min LVL</div>
		  														<div>{encounter.min_level}</div>
		  													</div>
		  												</React.Fragment>
	  												);
	  											}
	  											return (
	  												<div className="encounter" key={i}>
	  													<div className="flex">
	  														<div className="label">Method</div>
	  														<div>{encounter.method.name.replace(/-/g, " ")}</div>
	  													</div>
	  													<div className="flex">
	  														<div className="label">Chance</div>
	  														<div>{encounter.chance}%</div>
	  													</div>
	  													{level}
	  													{conditionElement}
	  												</div>
	  											);
	  										})
	  									}
	  								</div>
	  							</div>
	  						);
	  					})
	  				}
	  			</div>
	  		</div>
  		);
	}


		// use beatifly as test case
	EvolutionElement = () => {
		if (!this.state.evolutionMap.length) {
			return null; 
		}
		const pokemons = this.state.evolutionMap
			.sort((a,b) => a[0] - b[0])
			.map(([order, pokemon]) => pokemon);
		return (
			<div className="flex-column evolution-chain">
				<div className="label">Evolution Chain</div>
				<div className="flex pokemon-evolutions">
				{
					pokemons.map(pokemon => {
						let id = pokemon.id.toString(10);
						for (let i = 0; i < 3 - id.length; i++) {
							id = "0" + id; 
						}
						id = "#" + id;
						return (
							<div 
								className="flex-column pokemon" 
								key={pokemon.id} 
								onClick={() => this.props.onSelection(pokemon.name)}>
								<img 
									src={pokemon.sprites.front_default} 
									alt={pokemon.name} />
								<div className="id">{id}</div>
								<div className="name">{pokemon.name}</div>
								<div className="flex button-container">
								{
									pokemon.types.map(type => {
										const name = type.type.name;
										return (
											<button className={"cancel " + name} key={name}>{name}</button>
										);
									})
								}
								</div>
							</div>
						);
					})
				}
				</div>
			</div>
		);
	}

	EggGroupElement = () => {
		if (!this.state.species || !this.state.species.egg_groups || 
			  !this.state.species.egg_groups.length) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Egg Groups</div>
				<div>
					{this.state.species.egg_groups.map(a => a.name.replace(/-/g, " ")).join(", ")}
				</div>
			</div>
		);
	}

	GenderRateElement =  () => {
		if (!this.state.species) {
			return null;
		}
		let gr = this.state.species.gender_rate;
		if (!gr) {
			gr = 0;
		}
		if (gr < 0) {
			gr = (
				<div className="flex">
					<div className="label">Genderless</div>
				</div>
			);
		} else {
			const female = round((gr/8)*100,2);
			const male = 100 - female; 
			gr = (
				<React.Fragment>
					<div className="flex">
						<div className="label">Male</div>
						<div>{male}%</div>
					</div>
					<div className="flex">
						<div className="label">Female</div>
						<div>{female}%</div>
					</div>
				</React.Fragment>
			)
		}
		return (
			<div className="pokemon-container gender-rate">
				<div className="flex-column">
					{gr}
				</div>
			</div>
		);		
	}

	DmgElement = () => {
		if (!this.state.types.length || this.state.types.length !== this.props.pokemon.types.length) {
			return null; 
		}
		const base = [
			['normal', 1],
			['fire', 1],
			['fighting', 1],
			['water', 1],
			['flying', 1],
			['grass', 1],
			['poison', 1],
			['electric', 1],
			['ground', 1],
			['psychic', 1],
			['rock', 1],
			['ice', 1],
			['bug', 1],
			['dragon', 1],
			['ghost', 1],
			['dark', 1],
			['steel', 1],
			['fairy', 1],
		];
		const dmgTaken = new Map(base); 
		this.state.types.forEach(type => {
			Object.keys(type.damage_relations).forEach(key => {
				const dmgType = dmgMap.get(key);
				if (dmgType) {
					type.damage_relations[key].forEach(type => {
						const { name } = type;
						if (dmgTaken.has(name)) {
							if (dmgType.isTaken) {
								dmgTaken.set(name,dmgTaken.get(name)*dmgType.num);
							}
						}
					});
				}
			});
		});
		return (
			<div className="flex-column dmg-component">
				<div className="dmg-title">Damage Taken</div>
				<div className="dmg-element flex">
					{
						Array.from(dmgTaken).map(([type,value]) => {
							return (
								<div className={"dmg-type flex-column " + type} key={type}>
									<div className="vertical-text">{type}</div>
									<div className="multiplier">x{dmgTaken.get(type)}</div>
								</div>
							);
						})
					}
				</div>	
			</div>
		);
	} 	

	render() {
		if (!this.state.pokemon) {
			return null;
		} else if (this.state.pokemon && this.state.initializing) {
			return LoaderElement(); 
		}
		return (
			<div className="pokemon-layout flex-column">
				<div className="flex top-container">
					<div className="basic-info">
						{this.NameElement()}
						{this.TypesElement()}
						{this.HeightElement()}
						{this.WeightElement()}
						{this.GenderRateElement()}
					</div>
					{this.SpritesElement()}
				</div>
				<div className="flex metadata">
					{this.StatsElement()}
					<div className="ability-move flex-column">
					  <div className="flex dialogs">
							{this.AbilitiesElement()}
							{this.HeldItemsElement()}
						</div>
					</div>
					<div className="metadata-2">
						{this.BaseExpElement()}
						{this.BaseHappinessElement()}
						{this.CaptureRateElement()}
						{this.EggGroupElement()}
						{this.GenerationElement()}
						{this.GrowthElement()}
						{this.HabitatElement()}
						{this.HatchElement()}
					 </div>
				</div>
				{this.EvolutionElement()}
				{this.DmgElement()}
				{this.TabsElement()}
			</div>
		);
	}

	componentDidUpdate(prevProps) {
		if (prevProps.pokemon.id !== this.props.pokemon.id) {
			this.updateAll();
		}
	}
}