import React from 'react';
import ReactDOM from 'react-dom';
import PokemonLayout from './components/pokemon-layout/pokemon-layout.js';
import Dropdown from './components/dropdown/dropdown';
import './index.css';
import 'semantic-ui-css/semantic.min.css';
import { P, get } from './util.module.js';

class Game extends React.Component {

  // Mounting Lifecycle ----------------------------------------------

  constructor(props) {
    super(props); 
    this.state = {
      pokemons: [], 
      types: [], 
      currentPokemon: null,
      inputValue: '',
    };
    P.getPokemonsList().then(
      response => this.setState({
        pokemons: response.results,
      })
    );
    P.getTypesList().then(
      response => this.setState({
        types: response.results,
      })
    );

    // TODO: Add types to be searchable and give its own layout
  }

  // Event Handlers --------------------------------------------------

  handleSelection = name => {
    P.getPokemonByName(name, (response,error) => {
      if (!error) {
        this.setState({
          currentPokemon: response,
          inputValue: response.name,
        }); 
      } else {
        console.error(error);
      }
    });
  }

  handleInputValue = (inputValue) => {
    this.setState({inputValue});
  } 

  // Methods ---------------------------------------------------------

  render() {
    let content;
    if (this.state.currentPokemon) {
      content = <PokemonLayout 
                  pokemon={this.state.currentPokemon} 
                  onSelection={this.handleSelection}/>;
    }
    return (
      <div className="index flex-column">
        <Dropdown
          placeHolder='Search Pokemon'
          input={this.state.inputValue}
          onSelection={this.handleSelection}
          onInputValue={this.handleInputValue}
          options={this.state.pokemons.map(pokemon => pokemon.name)}/>
        {content}
      </div>
    );
  }
}

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);
